header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;

}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
     
   
}





/* ======================================= CTA ========================================== */
.cta{
    margin-top: 2.5rem;
    display: flex;
         justify-content: center;
    gap: 1.3rem;
}                   
                                                                                                                     
.btn-primary { border-radius: 0.4rem;
    text-align: center;
    padding-top: 0.5rem;
                  
}


.btn-primary:hover{
    cursor: pointer;
    background: var(--color-primary);
} 

button a{
    overflow: hidden;
    color: black;
    font-size: 20px;
}
button a:hover{
    overflow: hidden;
    font-size: 20px;
}

/* ====================================== Header socials ================================== */

.header__social{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__social::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
    color: black;
}

/* ==================================== home-img ================================================ */

.home-img{
    background: var(--color-primary);
    width: 22rem;
    height: 22rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 2rem;
    overflow: hidden;
    /* padding: 5rem 1.5rem 1.5rem 1.5rem; */

}

/* =========================== scroll down ======================================= */



/* ================================= medium screen ==================================*/

@media screen and (max-width:1024px) {
    header{
        height: 100vh;

    }
}

/* ================================= small screen ==================================*/

@media screen and (max-width:600px) {
    header{
        height: 100vh;
    }
   
}